export default {
  name: "GoEasyAudioPlayer",
  props: ['src', 'duration'],

  data() {
    return {
      play: false
    };
  },

  methods: {
    playAudio() {
      this.play = true;
      this.$refs.audioPlayer.src = this.src;
      this.$refs.audioPlayer.load();
      this.$refs.audioPlayer.currentTime = 0;
      this.$refs.audioPlayer.play();
      setTimeout(() => {
        this.play = false;
      }, this.duration * 1000);
    }

  }
};